body, h1, h2, h3, h4, h5, h6 { margin: 0 }
body {height: 100%;}


%flex_center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}




// scroll
::-webkit-scrollbar{width: 10px}
::-webkit-scrollbar-track{
  background: #b1b1b1;
  border-left: 4px solid white;
  border-right: 4px solid white;
}
::-webkit-scrollbar-thumb{ background: #0d1e4b }


h3 { margin: 20px 0 }
.content {
  min-height: calc(100% - 65px - 100px);
  padding: 0 0 20px;
}
.container {
  padding: 0 10px;
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;
}


// HEADER =============================================================
header {
  background-color: #45d5ce;
  height: 65px;

  .header {
    @extend %flex_center;
    a {
      font-size: 20px;
      font-weight: 700;
      color: black;
      transition: all .2s ease;
        &:hover {
          transform: scale(1.1);
        }
      
    }
  }
}




// TOP =============================================================

.top {
  background: rgb(56, 56, 56) url('./img/house.jpg') center center/cover no-repeat;
  height: calc(100vh - 65px);

  &_text{
    text-align: center;
    height: 90%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: radial-gradient(50% 30% at 50% 55%, #000000, rgba(0, 0, 0, 0) 90%);
    h2 {
      padding: 0 15px 10px;
      margin-bottom: 10px;
      border-bottom: 3px solid orange;
    }
   
  }
}

// МЕТАЛЛОЧЕРЕПИЦА ===========================================================
.cards {
  @extend %flex_center;
  flex-wrap: wrap;

  &_item {
    margin: 20px;
    text-align: center;
    img {
      height: 200px;
    }
  }

}



// MIDDLE ===========================================================

.middle {
  
  &_roofing_materials p { text-indent: 20px }
  
  &_services {
    
    &_cards {
      display: flex;
      flex-wrap: wrap;
      
      &_item {
        width: 25%;
        border: 1px solid white;

        &_imgdiv { height: 300px }
        
        &_text {
          margin: 0;
          padding: 10px 5px;
          color: white;
          background: rgb(72, 16, 33);
        }

      }
    }
    
    h5 {
      padding: 20px;
      border-radius: 7px;
      text-align: center;
    }
  }


  &_sales {
    padding: 40px 0;
    @extend %flex_center;
    justify-content: space-evenly;
    img {
      width: 250px;
    }
  }
}


// MAPS ===========================================================

.maps {

  &_imgs {
    @extend %flex_center;
    justify-content: space-evenly;
    img {
      margin: 20px 0;
      width: 200px;
      border-radius: 10px;
      
      transition: all .2s ease;
      &:hover {
        background-color:rgb(206, 206, 206);
        transform: scale(1.2);
      }
    }
  }
}

div.leaflet-bottom.leaflet-right {display: none}




// FOOTER ===========================================================

footer {
  height: 100%;
  background: #333;
  color: white;
  padding: 15px 0;

  .container {
    @extend %flex_center;
  }
  .footer_contacts {
    // width: 100%;
  }
}


// MEDIA =========================================================================
@media (max-width: 1060px) {
  .middle_services_cards_item{ width: 33.3% }
}

@media (max-width: 712px) {
  .cards {
    max-width: 100%;
    height: 280px;
    overflow-y: hidden;
    flex-direction: column;

    &::-webkit-scrollbar{height: 10px}
  }
  .middle_services {margin-top: 20px}
  .middle_services_cards_item{ width: 50% }

}

@media (max-width: 736px) {

 .top_text h2 {font-size: 40px}
}
@media (max-width: 666px) {
  .maps_imgs{ 
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media (max-width: 560px) {
  .top_text h2 {font-size: 34px}
  .top_text h4 {font-size: 28px}

  h3 {font-size: 34px}
  .top {height: 65vh}

  .middle_sales { 
    flex-wrap: wrap;
    flex-direction: column;
  }
  .middle_services_cards_item{ width: 100% }

}

@media (max-width: 440px) {
  .container {
    padding: 0;
  }

  footer .container {
    flex-direction: column;
    align-items: unset;
  }
}

